<template>
	<div id="financeManage">
		<div>
			<div class="top">
				<span>商户名称：</span>
				<el-input class="name" size="small"></el-input>
				<el-button class="search" type="primary">查询</el-button>
			</div>
			<table>
				<tr>
					<th>序号</th>
					<th>名称</th>
					<th>余额</th>
					<th>状态</th>
					<th>操作</th>
				</tr>
				<tr v-for="item in 5">
					<td>1</td>
					<td>中冶集团</td>
					<td>300000</td>
					<td>正常</td>
					<td class="handle">
						<span @click="recharge" class="recharge">充值</span>
						<span @click="blockup" class="blockup">停用</span>
						<span @click="record" class="record">交易记录</span>
					</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
	import main from './main';
	export default {
		...main
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import "../../style/mixin.scss";
	@import "./style.scss";
</style>


